.page-footer {
    background-color: black;
    padding-top: 40px;
}
.footer-container{
    position: relative;
    padding-bottom: 40px;
}

.footer__flex {
    display: flex;
    justify-content: space-between;
    // align-items: center;

    @include for-mobile-and-tablet {
        flex-direction: column;
        // align-items: center;
    }
}
// LOCATION-------------------------
.footer-location{
    @include for-desktop {
            display: none;
        }
    @include for-mobile-and-tablet {
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
        align-items: center;
        }
}

.footer-location__map {
    @include for-tablet-only {
        border: 0;
        min-width: 680px;
        min-height: 328px;
    }

    @include for-mobile-only {
        border: 0;
        width: 357px;
        height: 328px;
    }
}
.footer-location__title {
     @include for-mobile-and-tablet {
        font-family: PT Serif Caption;
        text-transform: uppercase;
        font-size: 36px;
        font-weight: 400;
        line-height: 48px;
        letter-spacing: 0.03em;
        text-align: center;
        margin-bottom: 4px;
    }

    @include for-mobile-only {
        font-size: 20px;
        line-height: 27px;
    }
}


// NAV-LIST----------------------------
.footer-nav__list{
display: flex;
@include for-mobile-and-tablet {
        display: none;
    }
}
.footer-nav__item:not(:last-child) {
        margin-right: 16px;
    }

.footer-nav__link {
    font-family: 'PT Serif Caption';
    color: var(--text-color);
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 0.03em;
    text-decoration-line: none;
    padding: 16px 0;

    &:hover,
    &:focus {
        color: var(--accent-color);
    }
}

// ADRESS-LIST------------------------
.contact-info {
    @include for-tablet-only {
        width: 680px;
        margin-bottom: 88px;
    }
    @include for-mobile-only {
        margin-bottom: 78px;
    }
}

.address-list {
    display: flex;
    flex-direction: column;

    &__item:not(:last-child) {
            margin-bottom: 20px;
        }

    @include for-mobile-only {
    &__item:not(:last-child) {
            margin-bottom: 16px;
        }
    }
   
}
.address-list__link {
    font-family: 'PT Serif Caption';
    color: var(--text-color);
    font-weight: 400;
    letter-spacing: 0.05em;
    text-decoration-line: none;
    font-style: normal;

    @include for-desktop {
        font-size: 16px;
        line-height: 21px;
        }

    @include for-tablet-only {
        font-size: 22px;
        line-height: 29px;
    }

    @include for-mobile-only {
        font-size: 16px;
        line-height: 21px; 
    }

    &:hover,
    &:focus {
        color: var(--accent-color);
    }
}


// ЛОГОТИПИ ФУТЕРА
.law {
    font-family: 'PT Serif Caption';
    color: var(--text-color);
    font-weight: 400;
    letter-spacing: 0.05em;
    
    @include for-desktop {
        position: absolute;
        bottom: 0;
        right: 0;

        margin-right: 80px;
        margin-bottom: 47px;
        
        font-size: 14px;
        line-height: 19px;
    }
    @include for-tablet-only{
        font-size: 20px;
        line-height: 27px;
        text-align: center;
    }
     @include for-mobile-only{
        font-size: 16px;
        line-height: 21px;
        text-align: center;
    }
}
.footer-logo {
    @include for-desktop {
        height: 149px;
        width: 294px;
        margin: -167px auto 16px;
    }
}

.footer-logo__link {
    fill: var(--text-color);
    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}

.footer-insta {
    margin: 6px auto 0px;
        text-align: center;

    @include for-mobile-and-tablet {
        display: none;
    }
}
.footer-insta__link {
    fill: var(--text-color);
    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}
.icon-footer {
    margin-right: 12px;
    padding: 0px;

}
